<template>
  <div class="container">
    <el-row>
      <el-input v-model="submitInfo.task.taskTitle" placeholder="请输入任务标题"></el-input>
    </el-row>
    <el-row>
      <el-input
        v-model="submitInfo.task.taskDetail"
        type="textarea"
        :rows="24"
        placeholder="请输入任务描述"
      ></el-input>
    </el-row>
    <br />
    <el-row type="flex">
      <el-col :span="8" class="block">
        <span class="demonstration">设置截止日期：</span>
        <el-date-picker v-model="submitInfo.task.deadLine" type="date" placeholder="选择日期"></el-date-picker>
      </el-col>
      <el-col :span="6" :offset="1">
        <el-button @click="submitTask" style="width: 100%" type="primary">提交/打卡</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "PublicTask",
  data() {
    return {
      submitInfo: {
        adminId: '',
        task: {
          taskTitle: "",
          taskDetail: "",
          deadLine: ""
        }
      }
    };
  },
  methods: {
    submitTask() {
      console.log("submitTask");
      let { adminId, task } = this.submitInfo;
      this.$axios({
        url: "",
        method: "post",
        data: {
          adminId,
          task
        }
      })
        .then(res => {
          console.log(res);
          if (res.dta.err === 0) {
            this.$mesthis.$message({
              message: res.data.msg,
              type: "success"
            })
          } else {
            this.$mesthis.$message({
              message: res.data.msg,
              type: "err"
            })
          }
        })
        .catch(err => {
          alert("网络异常，请重试！");
        });
    }
  }
};
</script>